import React from 'react';
import {Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb,BreadcrumbItem,
        Button, Modal, ModalHeader, ModalBody, Label, Row, Col} from 'reactstrap';
import {Control, LocalForm, Errors} from 'react-redux-form';
import {Link} from 'react-router-dom';
import {Loading} from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required= (val)=> val && val.length;
const minLength= (len)=> (val)=> val && (val.length>=len);
const maxLength= (len)=> (val)=> !(val) || (val.length<=len);

 class TheCommentModal extends React.Component {
     constructor(props) {
         super(props);

        this.state={
            isModalOpen:false,
        }
        this.toggleModal=this.toggleModal.bind(this);
        this.handleSubmit=this.handleSubmit.bind(this);
    };

    toggleModal(){
        this.setState({
            isModalOpen: !this.state.isModalOpen,
        });    
    }

    handleSubmit(values){
        this.toggleModal();
        this.props.postComment (this.props.dishId, values.rating, values.author, values.comment);
        //console.log("Current state is: "+JSON.stringify(values));
        //alert("Current state is: "+JSON.stringify(values));
    }

    render(){
        return(
            <>
            <Button outline onClick={this.toggleModal}>
                <span className="fa fa-pencil fa-lg"></span>Submit Comment
            </Button>
            <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                <ModalBody>
                    <LocalForm onSubmit={(values)=> this.handleSubmit(values)}>
                        <Row className="form-group">
                            <Label htmlFor="rating" md={12}>Rating</Label>
                            <Col md={12}>
                                <Control.select model=".rating" name="rating" className="form-control">
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                </Control.select>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="author" md={12}>Your Name</Label>
                            <Col md={12}>
                                <Control.text model=".author" name="author" placeholder="Your Name"
                                id="author" className="form-control"
                                validators={{
                                    required, minLength: minLength(3), maxLength: maxLength(15)
                                }}
                                />
                                <Errors className="text-danger" model=".author" show="touched"
                                messages={{
                                    required: 'Required ',
                                    minLength: "Must be greater than 2 characters",
                                    maxLength: "Must be 15 characters or less"
                                }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="comment" md={12}>Comment</Label>
                            <Col md={12}>
                                <Control.textarea model=".comment" id="comment" name="comment"
                                rows="6" className="form-control"/>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Col md={12}>
                                <Button type="submit" color="primary">
                                Submit
                                </Button>
                            </Col>
                        </Row>
                    </LocalForm>
                </ModalBody>
            </Modal>
            </>
        );
    }
}
    
    function RenderDish({SelDish}){
        return (
            <div key={SelDish.id} className="col-12 col-md-5 m-1">
                <FadeTransform in 
                transformProps = {{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                    <Card>
                    <CardImg top src={baseUrl + SelDish.image} alt={SelDish.name} />
                        <CardBody>
                            <CardTitle>{SelDish.name}</CardTitle>
                            <CardText>{SelDish.description}</CardText>
                        </CardBody>
                    </Card>
                </FadeTransform>
            </div>
        );
    }

    function RenderComments({TheComments, postComment, dishId}){
        const commentsection = TheComments.map(comment=>{
            return(
                <Fade in>
                    <li key = {comment.id}>
                        <p>{comment.comment}</p>
                        <p>-- {comment.author}, {" "} 
                        {
                            new Intl.DateTimeFormat('en-US', {
                                year: 'numeric',
                                month: 'short',
                                day: '2-digit'
                            }).format(new Date(comment.date))
                        }
                        </p>
                    </li>
                </Fade>
            )
        })
        return(
            <div className="col-12 col-md-5 m-1">
                <h4>Comments</h4>
                <ul className="list-unstyled">
                    <Stagger in>
                        {commentsection}
                    </Stagger>
                    <TheCommentModal dishId={dishId} postComment={postComment}/>
                </ul>
            </div>
        );
    }

    const Dishdetail = (props) => {

        if(props.isLaoding) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading/>
                    </div>
                </div>
            );
        }
        else if (props.errMess){
            return (
                <div className ="container">
                    <div className="row">
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if(props.TheDish!=null)
        {
            return (
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                                <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                                <BreadcrumbItem active>{props.TheDish.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3>{props.TheDish.name}</h3>
                            <hr/>
                        </div>
                    </div>
                    <div className="row">
                        <RenderDish SelDish={props.TheDish} />
                        <RenderComments TheComments={props.comments} 
                        postComment={props.postComment}
                        dishId={props.TheDish.id}/>
                    </div>
                </div>
            );
        }
        else{
            return(
                <div></div>
            );
        }
    }



export default Dishdetail;